  border-left: 2px solid rgba(40, 54, 69, 0.1); }

@media only screen and (min-width: 660px) and (max-width: 1199px) {
  .page-main {
    margin-top: -49px; } }

@media only screen and (min-width: 1200px) {
  .page-main {
    margin-top: -42px; } }

.sprite-logo {
  fill: #ffffff; }
  @media only screen and (min-width: 660px) and (max-width: 1199px) {
    .sprite-logo {
      display: none; } }
  @media only screen and (min-width: 1200px) {
    .sprite-logo {
      display: none; } }

.sprite-logo-tablet {
  fill: #ffffff; }
  @media only screen and (max-width: 659px) {
    .sprite-logo-tablet {
      display: none; } }
  @media only screen and (min-width: 1200px) {
    .sprite-logo-tablet {
      display: none; } }

.sprite-logo-desktop {
  fill: #ffffff; }
  @media only screen and (max-width: 659px) {
    .sprite-logo-desktop {
      display: none; } }
  @media only screen and (min-width: 660px) and (max-width: 1199px) {
    .sprite-logo-desktop {
      display: none; } }

.sprite-logo-footer {
  fill: #283645; }
  @media only screen and (min-width: 1200px) {
    .sprite-logo-footer {
      display: none; } }

.sprite-logo-footer-desktop {
  fill: #283645; }
  @media only screen and (max-width: 659px) {
    .sprite-logo-footer-desktop {
      display: none; } }
  @media only screen and (min-width: 660px) and (max-width: 1199px) {
    .sprite-logo-footer-desktop {
      display: none; } }

.sprite__academy-logo {
  width: 27px;
  height: 34px;
  fill: #d22856; }
  @media only screen and (min-width: 660px) and (max-width: 1199px) {
    .sprite__academy-logo {
      height: 37px; } }

