@media only screen and (min-width: 660px) and (max-width: 1199px) {
  .sprite-heart {
    transform: translate(-30px, -2px); } }

@media only screen and (min-width: 1200px) {
  .sprite-heart {
    transform: translate(-31px, -2px); } }

@media only screen and (min-width: 660px) and (max-width: 1199px) {
  .sprite-heart--top {
    transform: translateY(1px); } }

@media only screen and (min-width: 1200px) {
  .sprite-heart--top {
    transform: translateX(-31px); } }

.sprite-open {
  fill: #ffffff; }

.sprite-open--hidden {
  display: none; }

.sprite-close {
  fill: #ffffff;
  display: none; }

.sprite-close--active {
  display: block;
  transform: translate(28px, 2px); }
