.reviews__wrapper {
  text-align: center;
  box-sizing: border-box;

  @media #{$mobile} {
    max-width: 320px;
    margin: 0 auto;
    padding: 0 20px;
  }

  @media #{$tablet} {
    max-width: 660px;
    margin: 0 auto;
    padding: 0 20px;
  }

  @media #{$desktop} {
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 130px;
    background: url("../../img/quotes.png") no-repeat top 72px center;
  }
}

.reviews__item {
  padding-top: 40px;

  @media #{$tablet} {
    display: flex;
    flex-direction: column;
    padding-top: 82px;
  }

  @media #{$desktop} {
    display: flex;
    flex-direction: column;
    padding-top: 185px;
  }
}

.reviews__user {
  font-size: 18px;
  font-weight: 700;
  margin: 0;
  color: $black;

  @media #{$tablet} {
    font-size: 20px;
    order: 1;
  }

  @media #{$desktop} {
    font-size: 24px;
    order: 1;
    line-height: 30px;
    padding-top: 36px;
  }
}

.reviews__text {
  @media #{$mobile} {
    max-width: 280px;
    line-height: 30px;
  }

  @media #{$tablet} {
    font-size: 18px;
    max-width: 620px;
    margin-bottom: 34px;
  }

  @media #{$desktop} {
    font-size: 18px;
    max-width: 688px;
    margin: 0 auto;
  }
}

.reviews__text:nth-child(2) {
  @media #{$mobile} {
    display: none;
  }

  @media #{$tablet} {
    order: 2;
    margin: 0;
    font-size: 14px;
  }

  @media #{$desktop} {
    order: 2;
    padding-top: 2px;
  }
}

.reviews__toggles {
  @media #{$desktop} {
    margin: 0;
    position: relative;
  }
}
